import { IonCard, IonContent, IonPage, IonSearchbar } from "@ionic/react";
import React from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router";
import { checkAuthenticated, load_user } from "./actions/auth";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
import "./App.css";
import axios from "axios";

const MapView = ({ isAuthenticated, load_user }) => {
  const [name, setName] = React.useState([]);
  const [spots, setSpots] = React.useState([]);

  React.useEffect(() => {
    load_user().then((data) => setName(data));
    getSpots().then((data) => setSpots(data));
  }, []);

  if (!isAuthenticated) {
    return <Redirect to="/greet" />;
  }

  const getSpots = () => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    };
    return axios({
      url: `${process.env.REACT_APP_API_URL}/spot/`,
      method: "get",
      headers: config,
    }).then((response) => {
      console.log(response);
      return response.data;
    });

    /*try {
      const res = await axios.get(
        `${process.env.REACT_APP_API_URL}/spot/`,
        config
      );
    } catch (err) {
      console.log("Error:" + err);
    }
    */
  };

  return (
    <IonPage>
      <IonContent>
        <IonSearchbar color="primary" animated={true}></IonSearchbar>
        <IonCard>
          <MapContainer
            center={[51.505, -0.09]}
            zoom={13}
            scrollWheelZoom={false}
          >
            <TileLayer
              attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            <Marker position={[51.505, -0.09]}>
              <Popup>
                A pretty CSS3 popup. <br /> Easily customizable.
              </Popup>
            </Marker>
          </MapContainer>
        </IonCard>
        <h1>Hello {/*name["first_name"]*/}!</h1>
      </IonContent>
    </IonPage>
  );
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { checkAuthenticated, load_user })(
  MapView
);
